              h4 Tamao y capacidad del mismo
              p.mb-0 Calcular el tamao de un empaque suele ser uno de los problemas ms comunes en su diseo. Por lo general se realizan cambios en el prototipo por la dificultad de calcular el volumen de los productos, por ejemplo, se realiza una caja para zapatos; sin embargo, se debe tener en cuenta que las tallas para adulto van del nmero 34 hasta el 44, aproximadamente. 

      .col-md-6.col-xl-4.mb-4
