          .tarjeta.bg-color-primario2.p-4(titulo="Prototipo estructural")
            h4 Prototipo estructural 
            p Se encarga de aproximarse mucho ms al producto final, escogiendo los materiales, el tipo de impresin o troquelado, creando el empaque que satisfaga las necesidades del cliente y por supuesto, teniendo en cuenta el producto que va a contener, as como las caractersticas de la materia prima en que se elaborar, tipo de cierre, estilo de caja, acabados y el uso final del mismo. Es un prototipo ms elaborado y cercano al producto final.
          .tarjeta.bg-color-primario2.p-4(titulo="Prototipo operacional")
