        p Como el proceso de prototipado puede resultar muy costoso debido a las herramientas utilizadas y a la elaboracin de muchas pruebas, se recomienda evaluar la necesidad del prototipo y qu tan importante es que se previsualice con el mximo realismo el producto terminado. 
        h5 Los prototipos se utilizan para reducir el margen de riesgo, la incertidumbre del cliente respecto al producto terminado y su viabilidad, el aspecto y la percepcin del producto a travs de su vista previa.
        p Asimismo, el prototipo permite seleccionar la propuesta de diseo a travs de 3D, donde se pueden verificar las aristas, superficies, estabilidad, empaque, las zonas de etiquetado, los colores, los dobleces, los cdigos de barra, la revisin de los acabados, el anlisis de las dimensiones, el ensamble y la capacidad interna del producto y la usabilidad, entre otros aspectos, etc. 
