          p.mb-0 Teniendo un prototipo se puede realizar la presentacin al cliente para conocer su satisfaccin, as como estudios con los consumidores. El anlisis de estos aspectos permitir ajustar el prototipo, lo que conduce a tener un producto final de excelente calidad y as reducir el margen de error y ahorrar en costos, pues se tomarn buenas decisiones desde la etapa de creacin hasta la produccin final.
            br
            | Hoy se habla de los <em>dummies</em> que son prototipos a escala natural de un producto, conservando todas las caractersticas visuales, de diseo, color, textura, dimensiones y son un medio muy importante para comunicar una marca o un producto. Tambin se habla con frecuencia de los <em>renders</em>, que son prototipos que consisten en una visualizacin digital en 3D de un producto.
