        p El elemento ms importante de contacto con el consumidor es el empaque, pues constituye un motivo para la venta del producto, al atraer la atencin y la curiosidad del comprador. El diseo del empaque da un valor agregado al producto, ya que es vital que lo identifique y sea armonioso con el contenido. Es trascendental que se adece a las necesidades del cliente por su practicidad, ergonoma, seguridad e higiene, pues genera emociones que llevan al posicionamiento de una marca y se convierte en un componente del producto, y coadyuva en la exhibicin y competitividad en los estantes. El diseo del empaque vara desde un envoltorio en plstico o papel, hasta una caja o tambor de madera o acero. El diseo determina si el empaque es primario, el cual contiene al producto, secundario si contiene uno o ms paquetes primarios o terciario, si contiene uno o ms paquetes secundarios.
        
    p.mb-4(data-aos="fade") El empaque debe ser atractivo para el comprador, por lo que al disear es decisivo tener en cuenta lo que se presenta a continuacin:

