      .h2 2.1  Tipos y clasificacin

    p.mb-4(data-aos="fade") Los tipos de empaques suelen diferenciarse a partir del producto o elemento que contienen o envasan, en relacin con sus caractersticas y cantidad. Los tipos de envases son: 

