          .col-lg-2.col-md-3.col-4.mb-4.mb-md-0
            img.px-md-3(src='@/assets/curso/tema2-1-ico3.svg', alt='Imagen decorativa')
          .col-lg-9.col-md-8
            h4 Embalaje
            p Contiene varias unidades de un producto y prepara la carga de la forma ms adecuada para el modo de transporte elegido para su despacho. El embalaje ofrece algunas ventajas como: 
            ul.lista-ul--color.mb-0
              li.mb-0
                i.fas.fa-chevron-right
                | Facilitar el traslado de la carga y permitir su transporte en las mejores condiciones, segn el modo que se utilice.
              li.mb-0
                i.fas.fa-chevron-right
                | Manipular de manera adecuada los productos. 
              li.mb-0
                i.fas.fa-chevron-right
                | Facilitar el almacenamiento.
              li.mb-0
                i.fas.fa-chevron-right
                | Facilitar las condiciones idneas para su distribucin.   

    Separador


    // 2.2
    #t_2_2.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      .h2 2.2  Funcionalidad y usos

    p.mb-4(data-aos="fade") Los empaques pueden ser de dos tipos: 

    .row.tarjeta--container.mb-4
      .col-md.tarjeta.color-secundario.text-center.p-5(data-aos="fade-right")
        .row.justify-content-center.mb-4
          .col-12
            figure
              img(src='@/assets/curso/tema2-2-tarj1.png', alt='Texto que describa la imagen')  
        h1 Empaque natural
        p Est compuesto principalmente por materiales o medios amigables con el medio ambiente. Un ejemplo claro de este tipo de empaques se encuentra en la misma naturaleza, como la envoltura natural de la mazorca de maz. 
      .col-md.tarjeta.color-primario.text-center.p-5(data-aos="fade-left")
        .row.justify-content-center.mb-4
          .col-12
            figure
              img(src='@/assets/curso/tema2-2-tarj2.png', alt='Texto que describa la imagen')  
        h1 Empaque artificial
        p Es todo aquel que se obtiene a partir de un proceso generado por la transformacin del hombre y cuya materia prima es tomada de la naturaleza. Ejemplo de este tipo de recursos son los cartones, el papel o los polmeros. 

    p.mb-4(data-aos="fade") A continuacin, conozca algunas funcionalidades y usos de acuerdo con el tipo de empaque utilizado (Madererianasa, 2020): 

    .row.justify-content-center.mb-4(data-aos="fade-down") 
      .col-md-10.tarjeta.bg-suave
        SlyderE.py-4(indicadores="derecha")
          .row.justify-content-center
            .col-md-6.mb-4.mb-md-0
              h4 Empaque de papel
              p Se utiliza como un envoltorio que se encuentra dentro o fuera de otros empaques como bolsas, botes y cajas plegables de cartoncillo.
            .col-md-5
              figure
                img(src='@/assets/curso/tema2-2-slide1.png', alt='Empaque de papel')
          .row.justify-content-center
            .col-md-6.mb-4.mb-md-0
              h4 Empaque de cartn corrugado
              p Este tipo de empaque se usa para calzado, frutas y hortalizas, artesanas, decoracin, maquinaria industrial, electrodomsticos, mercancas a semigranel, entre otros.
            .col-md-5
              figure
                img(src='@/assets/curso/tema2-2-slide2.png', alt='Empaque de cartn corrugado')
          .row.justify-content-center
            .col-md-6.mb-4.mb-md-0
              h4 Empaque metlico
              p Los materiales que se utilizan son el acero y el aluminio, funcionan para alimentos enlatados o bebidas con gas. Estos empaques son resistentes, protegen los productos.
            .col-md-5
              figure
                img(src='@/assets/curso/tema2-2-slide3.png', alt='Empaque metlico')
          .row.justify-content-center
            .col-md-6.mb-4.mb-md-0
              h4 Empaque de plstico
              p Se utilizan en alimentos, cosmticos, productos de aseo, aceites, entre otros, los materiales que se usan son PET, PEAD, PP, PVC, entre otros. Los ms comunes son bolsas, frascos, tubos, cajas, botes, bandejas.
            .col-md-5
              figure
                img(src='@/assets/curso/tema2-2-slide4.png', alt='Empaque de plstico')  
          .row.justify-content-center
            .col-md-6.mb-4.mb-md-0
              h4 Empaque de vidrio
              p Los ms comunes son las botellas y frascos que son de un cuerpo transparente, slido y frgil. Permiten la visibilidad del contenido, como bebidas, salsas, productos cosmticos o farmacuticos y son reutilizables. 
            .col-md-5
              figure
                img(src='@/assets/curso/tema2-2-slide5.png', alt='Empaque de vidrio')     
          .row.justify-content-center
            .col-md-6.mb-4.mb-md-0
              h4 Empaque de madera
              p De todos los tipos de empaque que ms se utilizan el de madera suele ser el ms solicitado cuando se trata de exportar productos pesados. Tambin funciona cuando se requieren empaques hechos a la medida, debido a que brinda una mejor proteccin durante la transportacin. Utilizar la madera ofrece otros beneficios como tener mayor resistencia, ser reutilizable y tener un control sanitario adecuado. 
            .col-md-5
              figure
                img(src='@/assets/curso/tema2-2-slide6.png', alt='Empaque de madera')                                 


    p.mb-4(data-aos="fade") El empaque es importante porque protege al producto de los siguientes riesgos:

    .row.justify-content-center
      .col-md-8 
        .row
          .col-md-6.col-lg-6.mb-4
            .tarjeta-avatar
              img(src='@/assets/curso/tema2-2-ico1.svg' alt='Mecnicos')
              .tarjeta.bg-acento-botones
                .text-center.p-4
                  h5 Mecnicos
                  p Protegen en el momento de la carga y embalaje al producto, de sobrecargas, golpes y maltrato generado en el almacenamiento y distribucin, deterioro en el <em>set</em> de exhibicin. 
          .col-md-6.col-lg-6.mb-4
            .tarjeta-avatar
              img(src='@/assets/curso/tema2-2-ico2.svg' alt='Qumicos')
              .tarjeta.bg-color-primario2
                .text-center.p-4
                  h5 Qumicos
                  p Protegen de contaminacin aerobia o anaerobia de microbios, virus y dems patgenos que puedan estar en el ambiente, y de la reaccin de los componentes de un empaque con otro, por exposicin con diferentes materiales.
          .col-md-6.col-lg-6.mb-4
            .tarjeta-avatar
              img(src='@/assets/curso/tema2-2-ico3.svg' alt='Trmicos')
              .tarjeta.bg-acento-contenido2
                .text-center.p-4
                  h5 Trmicos
                  p Protegen de los cambios abruptos de temperatura durante el transporte o exhibicin, y a la exposicin de fro o calor excesivo, a veces por mal funcionamiento de los aparatos electrnicos. 
          .col-md-6.col-lg-6.mb-4
            .tarjeta-avatar
              img(src='@/assets/curso/tema2-2-ico4.svg' alt='Comerciales')
              .tarjeta.bg-suave
                .text-center.p-4
                  h5 Comerciales
                  p Protege durante la exhibicin y promocin del producto en el rea de venta, por maltrato del empaque pueden ser sustrados, destapados, probados, alterados y manipulados.  

    p.mb-4(data-aos="fade") Y cumple la funcin de proteccin al:

    .row
      .col-md-4.mb-4(data-aos="fade-right")
        img(src='@/assets/curso/tema2-2-img.png', alt='Imagen decorativa')
      .col-md-8.mb-4(data-aos="fade-left")
        TabsC.color-acento-botones
          .tarjeta.bg-color-primario2.p-4(titulo="Producto")
            h4 Producto
            p El empaque protege principalmente las caractersticas esenciales del producto como lo pueden ser el color, olor, textura, calidad, peso y sus cualidades organolpticas. 
          .tarjeta.bg-color-primario2.p-4(titulo="Consumidor")
            h4 Consumidor
            p Permite que el consumidor encuentre un producto con caractersticas fsicas y microbiolgicas de buena calidad, en el caso de los alimentos, con el peso y precio justo.
          .tarjeta.bg-color-primario2.p-4(titulo="Humano")
            h4 Humano
            p El empaque debe contar con medidas estndar de calificacin que permitan garantizar la estabilidad organolptica del producto, sea un alimento o un producto mdico. 
          .tarjeta.bg-color-primario2.p-4(titulo="Productor")
            h4 Productor
            p Beneficia el trabajo e inversin que realiza el productor, para hacer que sus productos lleguen al mercado, sean apetecidos, valorados y vendidos. 
          .tarjeta.bg-color-primario2.p-4(titulo="Comercializador")
            h4 Comercializador
            p Por el cuidado que provee la mercanca puede obtener un mejor margen de ganancia dentro de la cadena de venta, al tener productos de buena calidad en un excelente empaque. 
          .tarjeta.bg-color-primario2.p-4(titulo="Distribuidor - transportador")
            h4 Distribuidor - transportador
            p Podrn realizar el trabajo de una manera ms rpida, gil y segura, debido a que un empaque adecuado puede ahorrar tiempos de movilizacin y mano de obra. 
          .tarjeta.bg-color-primario2.p-4(titulo="Medio ambiente")
            h4 Medio ambiente
            p Se debe propender por garantizar que el empaque cause el menor impacto ambiental, tratando de que sus materiales sean biodegradables o reciclables.
          .tarjeta.bg-color-primario2.p-4(titulo="Asegurador")
            h4 Asegurador
            p Es posible que los empaques con un diseo pensado en las necesidades de bodegaje, transporte y distribucin generen menos riesgos y accidentes dentro de los espacios de trabajo.
          .tarjeta.bg-color-primario2.p-4(titulo="Vendedor")
            h4 Vendedor
            p En el rea de estanteras de ventas un empaque con un diseo funcional y realizado con los materiales correctos permitir el ahorro de espacio, ser llamativo dentro de las dems marcas y as generar mejores ganancias al dueo.          
          .tarjeta.bg-color-primario2.p-4(titulo="Estado")
            h4 Estado
            p Se beneficia de todas las transacciones mercantiles y de venta que realicen los particulares, ya que por estas cobra impuestos e IVA, entre otros.

    .row.bg-elemento.justify-content-center.py-3.align-items-end(data-aos="fade-down") 
      .col-lg-2.col-md-3.col-5.mb-4.mb-md-0
        img.px-md-3(src='@/assets/curso/team2-2-img2.svg', alt='Icono decorativo')
      .col-lg-8.col-md-9
        .row.align-items-center
          .col-lg-10.col-md-8.mb-3.mb-md-0
            h4.mb-md-1 Principales materiales para envases, empaques y embalajes
            p Para conocer ms sobre los principales materiales para envases, empaques y embalajes revise muy atentamente el siguiente documento: 
          .col-lg-1.col-md-1
            a.boton.color-acento-botones.me-3(:href="obtenerLink('/downloads/Prototipado-Anexo_Materiales.pdf')" target="_blank" type="application/pdf")
              span Descargar
              i.fas.fa-file-download

