    // 2.3
    #t_2_3.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      .h2 2.3  Materiales

    .row
      .col-md-7.mb-4(data-aos="fade-right")
        .cajon.color-primario.p-4.mb-4 
          p.mb-0 En la industria se utiliza gran diversidad de materiales, especialmente derivados de fibras de celulosa procedentes de la pulpa de la madera.
        p La impresin flexogrfica es el mtodo que utiliza una plancha en relieve flexible que permite imprimir sobre diferentes tipos de sustrato; la industria del empaque ha tenido un gran desarrollo, lo que ha hecho que sus costos se reduzcan. En diseo, la seleccin apropiada del sustrato (papel, tela, material de reciclaje, plstico o metal) es muy importante. Se debe tener en cuenta al momento de elegir el sustrato, la terminacin o textura de la superficie, color, gramaje y caractersticas de la impresin. Igualmente, las planchas flexibles fcilmente se adaptan a superficies ligeramente desiguales por lo que se puede imprimir en sustratos como el cartn, sin ejercer mucha presin. Uno de los sustratos que se encuentran en el mercado es el cartn recubierto, que es la materia prima esencial para producir empaques plegables. 
      .col-md-5.mb-4(data-aos="fade-left")  
        img(src='@/assets/curso/tema2-3-img.png', alt='Imagen decorativa')

    p.mb-4(data-aos="fade") El cartn es un sustrato formado por la transposicin de papeles, como en capas, lo que le otorga mayor resistencia y dureza; existen bsicamente cuatro variedades de cartn recubierto: reciclado recubierto, cartn slido blanqueado, <em>kraft</em> no blanqueado recubierto y plegadizo para cajas, como se muestra a continuacin:

    .row.justify-content-center.mb-5(data-aos="fade-down") 
      .col-md-10.tarjeta.bg-suave2.py-5
        ImagenInfograficaB.color-acento-botones
          template(v-slot:imagen)
            figure.px-lg-5
              img.px-lg-5(src='@/assets/curso/team2-3-infog.svg', alt='Texto que describa la imagen')

          .row.justify-content-center.bg-color-primario2.py-4(x="41.5%" y="39.8%" tooltip="Clic para ver ms" numero="")
            .col-md-3.col-3.mb-4.mb-md-0
              img.px-lg-4(src='@/assets/curso/tema2-3-modal1.svg', alt='Cartn Reciclado Recubierto')
            .col-md-9
              h4 Cartn Reciclado Recubierto (CRR) o cartn dplex y de respaldo gris. En ingls se conoce como CCN y WLC
              p Esta referencia es la predominante en la industria, es importante destacar que cuando se est imprimiendo es menos flexible que otras variedades.
              p Es el cartn ms econmico que se usa en empaques que no requieran una excelente impresin, resistencia o limpieza, pues se elabora con una mezcla de fibras que han sido recicladas, del papel peridico impreso, cajas o contenedores corrugados, que tienen un recubrimiento multicapa. 
          .row.justify-content-center.bg-color-primario2.py-4(x="58.5%" y="39.8%" tooltip="Clic para ver ms" numero="")
            .col-md-3.col-3.mb-4.mb-md-0
              img.px-lg-5.px-3(src='@/assets/curso/tema2-3-modal2.svg', alt='Cartn slido blanqueado o CSB')
            .col-md-9
              h4 Cartn slido blanqueado o CSB
              p Tradicionalmente esta es la referencia ms costosa, este tipo de cartn puede tener un pigmento blanco con una capa de estuco que puede ser doble o triple en la parte superior, y una o dos en el reverso. El CSB, en la prensa, ofrece excelente impresin, buenas propiedades de formado y procesado y, como su estructura es homognea es ideal para la elaboracin de empaques premium, tiene gran consistencia y no tiene polvo de papel que le genere imperfecciones, por eso se usa en empaques de lujo. Asimismo, las fibras delgadas y cortas de esta madera hacen que se obtenga una superficie muy lisa, lo que ayuda a alcanzar empaques lujosos de alta calidad de impresin, se usa en productos cosmticos, farmacuticos, publicidad y artes grficas, instrumentos mdicos y cigarrillos, entre otros.
              p Tambin puede combinarse con otros materiales y utilizarse en la elaboracin de empaques para lquidos. Se utiliza especialmente para productos que requieran olor y gusto neutro. 
          .row.justify-content-center.bg-color-primario2.py-4(x="58.5%" y="59.8%" tooltip="Clic para ver ms" numero="")
            .col-md-3.col-3.mb-4.mb-md-0
              img.px-lg-5.px-3(src='@/assets/curso/tema2-3-modal3.svg', alt='Cartn recubierto no blanqueado')
            .col-md-9
              h4 Cartn recubierto no blanqueado (CRNB) o <em>Kraft</em>, o CNK o SUS en ingls
              p Este cartn es ms econmico que el CSB. Normalmente se fabrica exclusivamente con pasta qumica no blanqueada, de dos o tres capas de estuco en la cara superior, a veces se coloca una capa de estuco en el reverso. Los espesores de este cartn varan entre 14 y 30 puntos, por lo que son utilizados en empaques de alimentos congelados. 
          .row.justify-content-center.bg-color-primario2.py-4(x="41.5%" y="59.8%" tooltip="Clic para ver ms" numero="")
            .col-md-3.col-3.mb-4.mb-md-0
              img.px-lg-4(src='@/assets/curso/tema2-3-modal4.svg', alt='Cartn folding o para cajas plegables o FBB/GC/UC')
            .col-md-9
              h4 Cartn folding o para cajas plegables o FBB/GC/UC
              p Tambin se conoce como cartn cromo estucado, se fabrica a base de varias capas de fibras vrgenes cortas, gruesas y rgidas. Tiene mximo tres capas de estucado en el anverso y en algunos casos tambin est estucado en el reverso. Se utiliza en empaques en los que la rigidez en el empaque o envase son muy importantes. Sus aplicaciones grficas pueden ser postales y tarjetas o sobres para diferentes ocasiones, as como en empaques de productos de belleza, productos farmacuticos, cigarrillos, alimentos y envases sofisticados para dulces, confites y bebidas.       

    p.mb-4(data-aos="fade") Ahora, se estudiar otros materiales diferentes al papel:

    .row.justify-content-center
      .col-lg-10.col-md-9
        .row.justify-content-center.tarjetas-flip2(data-aos="fade-down")
          .col-lg-6.col-xl-6.mb-4
            .tarjeta.tarjeta-flip.color-primario
              .tarjeta-flip__contenedor
                .tarjeta-flip__img(:style="{'background-image': `url(${require('@/assets/curso/tema2-3-flip1.svg')})`}")
                  .tarjeta-flip__img-content
                    h4 Metal
                .tarjeta-flip__contenido.text-center.p-4
                  h4 Metal
                  p.mb-0 Se emplea como material rgido, especialmente para contener tanto elementos slidos como lquidos, ya que permiten un sellamiento total. Estos envases por lo general estn hechos en aluminio y hojalata; por sus caractersticas fsicas, este tipo de empaques ofrecen una mayor durabilidad y capacidad de conservacin, entre los productos que se pueden encontrar con este tipo de empaques estn las gaseosas, aceites, pinturas, dulces, entre muchos otros. 
          .col-lg-6.col-xl-6.mb-4
            .tarjeta.tarjeta-flip.color-primario(@mouseover="indicadorTarjetaFlip = false")
              .indicador--hover(v-if="indicadorTarjetaFlip")
              .tarjeta-flip__contenedor
                .tarjeta-flip__img(:style="{'background-image': `url(${require('@/assets/curso/tema2-3-flip2.svg')})`}")
                  .tarjeta-flip__img-content
                    h4 Vidrio
                .tarjeta-flip__contenido.text-center.p-4
                  h4 Vidrio
                  p.mb-0 Es utilizado por sus caractersticas especficas, ya que es una opcin reutilizable y muy higinica, la cual permite conservar las caractersticas organolpticas de vinos, medicamentos, entre otros. 
          .col-lg-6.col-xl-6.mb-4
            .tarjeta.tarjeta-flip.color-primario
              .tarjeta-flip__contenedor
                .tarjeta-flip__img(:style="{'background-image': `url(${require('@/assets/curso/tema2-3-flip4.svg')})`}")
                  .tarjeta-flip__img-content
                    h4 Textil
                .tarjeta-flip__contenido.text-center.p-4
                  h4 Textil
                  p.mb-0 Este tipo de materia prima es utilizada principalmente en bolsas promocionales y de almacenes de cadena. 
          .col-lg-6.col-xl-6.mb-4
            .tarjeta.tarjeta-flip.color-primario(@mouseover="indicadorTarjetaFlip = false")
              .indicador--hover(v-if="indicadorTarjetaFlip")
              .tarjeta-flip__contenedor
                .tarjeta-flip__img(:style="{'background-image': `url(${require('@/assets/curso/tema2-3-flip3.svg')})`}")
                  .tarjeta-flip__img-content
                    h4 Madera
                .tarjeta-flip__contenido.text-center.p-4
                  h4 Madera
                  p.mb-0 Utilizado principalmente en embalaje de productos, por su gran durabilidad, resistencia y proteccin.

