    // 2.4
    #t_2_4.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      .h2 2.4  Estructuras y diseo

    .row.justify-content-center
      .col-md-10
        .cajon.color-primario.p-4.mb-4 
          p.mb-0 Muchos productos por su naturaleza y caractersticas fsicas necesitan dnde reposar. Un buen empaque determina en gran medida el xito o fracaso de un producto, ms all de las cualidades y caractersticas del producto mismo, el primer contacto del cliente es de carcter visual, algo que es aprovechado por el diseo y el <em>marketing</em> en general. 

    p(data-aos="fade") Al crear el prototipo de cualquier empaque se deben identificar diversos factores concernientes al diseo y a su funcionalidad; aunque el empaque es lo primero que observa el consumidor, detrs de su desarrollo hay un sinfn de variables y problemticas por resolver. Ms all del diseo, la imagen o la forma, el diseo de empaques requiere incorporar en tiempos modernos una experiencia sensorial.

    p.mb-4(data-aos="fade") Antes de disear el empaque se debe tener en cuenta:

    .row  
      .col-lg-4.col-md-9.mb-4.d-flex
        .tarjeta-numerada.tarjeta2-4.color-acento-contenido.px-4.py-3
          .tarjeta-numerada__numero.text-white
