              img.px-md-2(src='@/assets/curso/tema2-4-ico3.svg', alt='Icono decorativo')
            .col-md-6.col-6
              h4.mb-0 Medios de transporte y condiciones de este

    p.mb-4(data-aos="fade") La ingeniera de empaques busca proteger de manera segura los productos, para ser embalados y transportados, estudiando su desarrollo, fallas estructurales y funcionales. Elementos como el uso, la competencia, las estrategias de mercado y los materiales que estn en auge se tienen en cuenta en la elaboracin del empaque, a partir de estos parmetros bsicos se empiezan a realizar bocetos y prototipos, los cuales pueden ir cambiando de aspecto durante este proceso. 

    .row.bg-elemento.justify-content-center.py-3.align-items-end.mb-4(data-aos="fade-down") 
      .col-lg-3.col-md-4.col-5.mb-4.mb-md-0
        img.px-md-3(src='@/assets/curso/tema2-4-img.svg', alt='Icono decorativo')
      .col-lg-7.col-md-7
        h4 Diversas preguntas nacen al inicio de este proceso, algunas de ellas:
        ul.lista-ul--color.mb-0
          li.mb-1
            i.fas.fa-chevron-right
            | Cul va a ser su uso?
          li.mb-1
