    // 2.5
    #t_2_5.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      .h2 2.5  Fases de produccin

    p.mb-4(data-aos="fade") El proceso de produccin grfica consta de muchas variables que hacen parte de la estrategia de produccin, entre estas se tienen: 

    TabsA.color-acento-botones.mb-4
      .row.tarjeta.bg-color-primario2.py-5.px-3(titulo="Planificacin estratgica")
        .col-md-6.mb-4
          h4 Planificacin estratgica
          p El primer paso para el desarrollo de cualquier proyecto en produccin grfica es el encuentro entre el diseador y el cliente, donde se realiza el <em>brief</em>, documento gua que describe el producto o servicio y su comercializacin. En esta fase es relevante establecer el presupuesto, los costos, sustratos, sistemas de impresin y tiempos establecidos de entrega y desarrollo de las piezas a trabajar. 
        .col-md-6
          img(src='@/assets/curso/tema2-5-slide1.png', alt='Planificacin estratgica')
      .row.tarjeta.bg-color-primario2.py-5.px-3(titulo="Propuesta creativa")
        .col-md-6.mb-4
          h4 Propuesta creativa
          p Es la propuesta que presenta el diseador como solucin al problema requerido por el cliente, a travs  de bocetos que se ajusten a los parmetros y estndares establecidos, y al presupuesto estipulado en la primera fase de produccin; en algunos casos primero se disea la pieza grfica para poder tener una idea ms cercana del presupuesto requerido. Asimismo, los acabados inciden en el precio, no es lo mismo una pieza que requiere un plastificado que un barnizado.
        .col-md-6
          img(src='@/assets/curso/tema2-5-slide2.png', alt='Propuesta creativa')
      .row.tarjeta.bg-color-primario2.py-5.px-3(titulo="Artes finales")
        .col-md-6.mb-4
          h4 Artes finales
          p Una vez establecidos todos los estndares del proyecto y aprobados los bocetos, el diseador se dispone a realizar las artes finales con la ayuda de programas de diseo. Es aqu donde aparece el proceso de preimpresin o preprensa, donde se revisa, ajusta y prepara el documento, normalmente en formato PDF para su reproduccin fsica, utilizando un sistema de impresin digital, <em>offset</em> o serigrafa. El arte final asegura que el proyecto sea exitoso o no. 
        .col-md-6
          img(src='@/assets/curso/tema2-5-slide3.png', alt='Artes finales')     
      .row.tarjeta.bg-color-primario2.py-5.px-3(titulo="Pruebas de color")
        .col-md-6.mb-4
          h4 Pruebas de color
          p Una prueba de color es una simulacin de los colores que pueden ser obtenidos por un sistema de impresin, para poder previsualizar el resultado final que se obtendr. Una correcta simulacin del resultado esperado en la impresin permitir realizar cambios de diseo o de color en un archivo antes de proceder a la impresin final (Martnez, s.f.).  
