    p(data-aos="fade") Como ya se ha mencionado, el empaque constituye una herramienta de mercadeo, distribucin y venta de un producto, porque coadyuva en fortalecer la imagen corporativa, posicionar la marca, influir en la decisin de compra por el consumidor; igualmente, el empaque sirve para proteger el producto, facilitando la manipulacin y la conservacin de este, preservando su calidad, adems que debe ser funcional, innovador y amigable con el medio ambiente. En general, el empaque est orientado hacia el <em>marketing</em> y debe cumplir con varios requisitos. 

    p.mb-4(data-aos="fade") Al tener en cuenta todos estos aspectos, dependiendo del tipo de producto para el que se disee el empaque, es necesario consultar las diferentes normas, que el Instituto Colombiano de Normas Tcnicas y Certificacin (ICONTEC) ha establecido para cada tipo de producto, en lo relacionado con el diseo del empaque, de conformidad con las Normas Tcnicas Colombianas (NTC). 

    .row 
      .col-md-5.mb-4(data-aos="fade-right")
        p Asimismo, se deben tener en cuenta las normas expedidas por el Organismo Internacional de Estandarizacin (ISO), igualmente, de la Superintendencia de Industria y Comercio (SIC), as como algunos ministerios que profieren normas relacionadas con el diseo tcnico de los empaques, que es necesario tener en cuenta antes de realizar el diseo respectivo. Todas estas normas deben aplicarse al diseo del empaque, especialmente si se trata de productos a exportar, debido a los altos estndares exigidos por numerosos pases.
      .col-md-7.mb-4(data-aos="fade-left")
        .cajon.color-primario.p-4.mb-4 
          p.mb-0 Por ejemplo, el Ministerio de Agricultura y Desarrollo Rural y el Ministerio de Comercio, Industria y Turismo por medio de la Resolucin 0224 de 2007 expidieron el Reglamento Tcnico Nro. RTC-002 MADR, por medio del cual regularon los requisitos mnimos que deben cumplir los empaques de los productos agrcolas para consumo humano que se importen, se produzcan y se comercialicen en el territorio colombiano. 

    p.mb-4(data-aos="fade") Igualmente, algunos ejemplos relacionados con las normas establecidas por el ICONTEC en relacin con el tema son:

    .row.mb-4
      .col-md-4.col-xl.mb-4.d-flex
        .tarjeta.bg-suave.p-4
          h4.text-center NTC 1257:
          .tarjeta__contenido
            p.text-center Establece los requisitos que deben cumplir las pelculas de polietileno de baja densidad para el empaque de alimentos, debe estar libre de burbujas, de contaminantes, de materias txicas, que no tengan color y establece su ancho mximo.
      .col-md-4.col-xl.mb-4.d-flex
        .tarjeta.bg-color-primario2.p-4
          h4.text-center NTC 1468:  
          .tarjeta__contenido
            p.text-center Estableci los requisitos que deben tener los ensayos a los cuales se deben someter los envases de papel o cartn que se usan para envasar la leche, lcteos y jugos. La norma habla del material en que se deben disear los empaques, las tintas a usar para su impresin, espesor y en general, el cuidado con la salud de las personas.
      .col-md-4.col-xl.mb-4.d-flex
        .tarjeta.bg-acento-botones.p-4
          h4.text-center NTC 512-1 y NTC 512-2:  
          .tarjeta__contenido
            p.text-center Establecen cmo deben ser rotulados o etiquetados en cuanto a sus elementos mnimos, que deben aparecer en los empaques de productos alimenticios.        

    p.mb-4(data-aos="fade") El no cumplimiento de estas normas implica la aplicacin de sanciones y multas y la inconformidad de los clientes, aunada a posibles prdidas econmicas.  

    .row.justify-content-center.mb-5(data-aos="fade-down") 
      .col-md-10
        .bloque-texto-g.color-primario.p-3.p-sm-4
