            p.mb-0 Por otra parte, el diseo del empaque debe tener en cuenta la etiqueta, cuya funcin es que el consumidor pueda identificar fcilmente el producto, all aparecen adems datos como la fecha de caducidad, informacin o tabla nutricional, instruccin sobre condiciones de uso, almacenamiento, nombre y direccin del fabricante, identificacin del lote, etc. El empaque puede contener el pas de origen, peso del producto, puerto de salida o de entrada, igualmente, contendr el cdigo de barras, que es un elemento indispensable, pues permite mantener el inventario actualizado y adems contiene informacin acerca de las caractersticas del producto.

