    p(data-aos="fade") En este punto es importante tener presente cules sern los medios de reproduccin que se van a utilizar en el proceso de impresin, el presupuesto formulado, la cantidad de ejemplares y acabados o terminados que dichas piezas van a requerir. 
    
    p.mb-4(data-aos="fade") Antes de pasar al procedimiento es importante revisar algunas definiciones bsicas referentes al proceso:

    .row.justify-content-center.mb-4(data-aos="fade-down") 
