          .row.justify-content-center
            .col-md-11
              h5.mb-0 <em>Traping</em>
              p.mb-0 Capacidad de una tinta de mezclarse sobre otra, o manipulacin para eliminar espacios en blanco entre colores (por dilatacin de papel o registro).  
          .row.justify-content-center
            .col-md-11
              h5.mb-0 Sobreimpresin
              p.mb-0 Superposicin de un color sobre otro (ayuda a evitar el desregistro) contaminndose entre s. 
          .row.justify-content-center
            .col-md-11
              h5.mb-0 Montaje
              p.mb-0 Distribucin de una hoja o pieza de diseo en un rea para fines de optimizacin de papel plegado. 
          .row.justify-content-center
            .col-md-11
              h5.mb-0 <em>Pantone</em>
              p.mb-0 Colores de simulacin de estndares de tintas, preparadas por fabricantes determinados. 
          .row.justify-content-center
            .col-md-11
              h5.mb-0 CTP (<em>Computer to plate</em> - del computador a la plancha)
              p.mb-0 Sistema de impresin de separaciones digitales en plancha. 
          .row.justify-content-center
            .col-md-11
              h5.mb-0 Formato de plancha
              p.mb-0 Tamao en mm, de las planchas referenciadas por el fabricante de la impresora litogrfica. 
          .row.justify-content-center
            .col-md-11
              h5.mb-0 Guas de corte
